.App {
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: var(--base-white);
}
.search {
  position: relative;
  background-color: var(--pure-white);
  border-radius: 20px;
}

.search input {
  outline: none;
  border: none;
  border-radius: inherit;
  padding: 8px;
  padding-left: 33px;
  font-size: 0.7rem;
  width: 220px;
  font-weight: 500;
}

.sidebar nav {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 40px;
}

.sidebar nav:hover {
  color: var(--theme-color);
  border: 1px solid var(--theme-color);
}

.sidebar nav.selected:hover {
  color: var(--pure-white);
}

.dashboard {
  width: 100%;
  flex: 1;
  overflow-y: auto;
  display: grid;
  grid-template-rows: repeat(25, 1fr);
  grid-template-columns: repeat(8, 1fr);
  gap: 12px;
}

.grid-item {
  text-align: center;
  border-radius: 10px;
}

.grid-item .title {
  font-size: 0.8rem;
  font-weight: 600;
}

.greeting-card {
  grid-column: 1/7;
  grid-row: 1/6;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.greeting-card .left {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  height: 100%;
  font-size: 0.65rem;
  font-weight: 600;
}

.greeting-card .left .date {
  font-size: 0.75rem;
  font-weight: 600;
}

.greeting-card .left .greet {
  font-size: 1.5rem;
  font-weight: 600;
}

.greeting-card .right {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-grow: 1;
  height: 100%;
  border-radius: 10px;
}

.greeting-card .right .mini-card {
  min-width: 180px;
  display: none;
  background-image: url("./assets/images/svg/logo-blue.svg");
  background-repeat: no-repeat;
  background-size: 30% 90%;
  background-position: 100% 60%;
  text-align: left;
}

.greeting-card .right .card {
  height: 80%;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: flex-start;
  background-color: var(--white-f8);
  width: 160px;
  border-radius: 10px;
  background-image: url("./assets/images/svg/logo-blue.svg");
  background-repeat: no-repeat;
  background-size: 30%;
  background-position: 100% 80%;
  font-size: 0.7rem;
}

.greeting-card .right .card + .card {
  margin-left: 15px;
}

.greeting-card .right .card .icon {
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.9rem;
  padding: 0.5rem;
}

.greeting-card .right .card .count {
  font-size: 1.5rem;
  font-weight: 600;
}

.greeting-card .right .card .desc {
  font-size: 0.6rem;
}

.premium-card {
  grid-column: 7/9;
  grid-row: 1/6;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  background-color: var(--blue-eb);
  /* min-width: 175px; */
}

.premium-card .bg-image {
  height: 100%;
  width: 60px;
  object-fit: cover;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.premium-card .content {
  display: flex;
  flex-direction: column;
  text-align: left;
  padding: 5px 10px;
}

.premium-card .content-title {
  font-size: 1rem;
  font-weight: 600;
}

.premium-card .content-desc {
  color: var(--black-83);
  font-size: 0.6rem;
  flex: 1;
}

.premium-card .subscription-btn {
  padding: 9px;
  font-size: 0.7rem;
  font-weight: 600;
  color: var(--pure-white);
  background-color: var(--theme-color);
  border: none;
  width: fit-content;
  border-radius: 3px;
  margin-top: 15px;
}

.KPI {
  grid-column: 1/4;
  grid-row: 6/15;
  background-color: var(--pure-white);
  display: flex;
  flex-direction: column;
}

.KPI .header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.KPI .drop-down {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  position: relative;
  background-color: var(--base-white);
  cursor: pointer;
}

.KPI .drop-down .drop-down-selected {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.7rem;
  font-weight: 600;
  color: var(--black-83);
}

.chevron {
  margin-left: 3px;
  font-size: 0.8rem;
  transition: transform 0.3s;
}

.open {
  transform: rotate(180deg);
}

.KPI .drop-down .drop-down-menu {
  position: absolute;
  top: 27px;
  z-index: 1;
  background-color: var(--base-white);
  width: 100%;
  text-align: left;
}

.KPI .drop-down .drop-down-menu .drop-down-item {
  font-size: 0.7rem;
  font-weight: 600;
  color: var(--black-83);
  padding: 3px 5px;
}

.KPI .drop-down .drop-down-menu .drop-down-item:hover {
  background-color: var(--theme-color);
  color: var(--pure-white);
}

.drop-down-menu .drop-down-item:first-child:hover {
  @apply rounded-t;
}

.drop-down-menu .drop-down-item:last-child:hover {
  @apply rounded-b;
}

.KPI .sub-details .current-value div {
  font-size: 1.25rem;
  font-weight: 600;
  width: fit-content;
}

.KPI .sub-details .current-value span {
  @apply bg-light-blue;
  display: flex;
  font-size: 0.5rem;
  justify-content: center;
  align-items: center;
  width: fit-content;
  padding: 2px 5px;
  border-radius: 9px;
  margin-bottom: 4px;
}

.KPI .sub-details .previous-value {
  font-size: 0.5rem;
  color: var(--black-83);
  text-align: left;
}

.KPI .area-chart {
  flex: 1;
  width: 100%;
  /* background-color: red; */
  color: var(--pure-black);
  margin-top: 3px;
}

.KPI .area-chart .area-chart-container {
  height: 100%;
  margin-left: -20px;
}

.x-axis-tick {
  fill: var(--black-83);
  font-size: 10px;
}

.y-axis-tick {
  fill: var(--black-83);
  font-size: 10px;
}

.custom-tooltip {
  background-color: var(--pure-black);
  color: var(--pure-white);
  font-size: 0.6rem;
  padding: 4px 6px;
  border-radius: 7px;
  z-index: 111;
}

.attendance {
  grid-column: 4/7;
  grid-row: 6/15;
  background-color: var(--pure-white);
  display: flex;
  flex-direction: column;
}

.attendance .header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.attendance .drop-down {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  position: relative;
  background-color: var(--base-white);
  cursor: pointer;
}

.attendance .drop-down .drop-down-selected {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.7rem;
  font-weight: 600;
  color: var(--black-83);
}

.attendance .legend-wrapper {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 10px 0px;
}

.attendance .legend {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.attendance .legend span {
  border-radius: 50%;
  height: 5px;
  width: 5px;
  content: "";
  margin-right: 3px;
}

.attendance .legend div {
  color: var(--black-83);
  font-size: 0.6rem;
  font-weight: 600;
}

.attendance .stacked-bar-chart {
  width: 100%;
  flex: 1;
}

.stacked-bar-chart .stacked-bar-chart-container {
  height: 100%;
  margin-left: -20px;
}

.task-card {
  grid-column: 1/3;
  grid-row: 15/26;
  display: flex;
  flex-direction: column;
  background-color: var(--pure-white);
}

.task-card .header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.task-wrapper {
  flex: 1;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  overflow-y: auto;
}

.task + .task {
  margin-top: 10px;
}

.task {
  width: 100%;
  background-color: var(--base-white);
  border-radius: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.task .task-title {
  font-size: 0.8rem;
  font-weight: 600;
}

.task .task-type {
  font-size: 0.65rem;
  font-weight: 600;
  color: var(--black-83);
}

.task .circular-progress-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.task .circular-progress-wrapper span {
  font-size: 0.6rem;
  position: absolute;
  color: var(--black-83);
  font-weight: 600;
}

.employee-satisfaction {
  grid-column: 3/5;
  grid-row: 15/26;

  background-color: var(--pure-white);
  display: flex;
  flex-direction: column;
}

.employee-satisfaction .header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.employee-satisfaction .score-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  height: fit-content;
  top: 35%;
}

.employee-satisfaction .score-card .score {
  font-size: 1.3rem;
  font-weight: 700;
}

.employee-satisfaction .score-card .score-desc {
  font-size: 0.65rem;
}

.employee-satisfaction .score-card .total-response {
  font-size: 0.45rem;
  color: var(--black-83);
}

.employee-satisfaction .stats-wrapper {
  width: 100%;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  overflow-y: auto;
  margin-top: 10px;
}

.stats-wrapper .stats {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: 100%;
}

.stats-details {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  text-align: left;
}

.stats-title {
  font-size: 0.8rem;
  font-weight: 600;
  width: 100%;
}
.stats-range {
  font-size: 0.65rem;
  color: var(--black-83);
  font-weight: 600;
}

.stats-wrapper .range-graph {
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
  @apply pt-2;
}

.stats-wrapper .range-graph input {
  width: 100%;
}

input[type="range"] {
  -webkit-appearance: none; /* Override default appearance */
  appearance: none;
  width: 70%;
  height: 1px; /* Adjust height */
  background: var(--white-f4); /* Track color */
  outline: none;
  opacity: 1; /* Track opacity */
}

/* input[type=range]:hover {
  opacity: 1;
} */

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none; /* Override default appearance */
  appearance: none;
  width: 25px; /* Thumb width */
  height: 15px; /* Thumb height */
  background: var(--blue-b0); /* Thumb color */
  cursor: pointer; /* Cursor on hover */
  border-radius: 3px; /* Rounded thumb */
}

input[type="range"]::-moz-range-thumb {
  border: none;
  width: 25px; /* Thumb width */
  height: 15px; /* Thumb height */
  background: var(--blue-b0); /* Thumb color */
  cursor: pointer; /* Cursor on hover */
  border-radius: 3px; /* Rounded thumb */
}

input[type="range"]::-ms-thumb {
  border: none;
  width: 25px; /* Thumb width */
  height: 15px; /* Thumb height */
  background: var(--blue-b0); /* Thumb color */
  cursor: pointer; /* Cursor on hover */
  border-radius: 3px; /* Rounded thumb */
}

.employment-status {
  grid-column: 5/7;
  grid-row: 15/26;
  background-color: var(--pure-white);
  display: flex;
  flex-direction: column;
}

.employment-status .header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.employment-status .pie-chart-area {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 380px;
  position: relative;
  margin-top: 5px;
}

.employment-status .pie-chart-area .total-count {
  font-size: 1.2rem;
  font-weight: 600;
}

.employment-status .pie-chart-area .total-cta {
  font-size: 0.42rem;
  font-weight: 600;
  color: var(--black-83);
}

.employment-status .stats-wrapper {
  width: 100%;
  overflow-y: auto;
  height: 100%;
  margin-top: 10px;
}
.employment-status .stats-wrapper .stats {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.employment-status .stats-wrapper .stats + .stats {
  margin-top: 7px;
}

.employment-status .stats-wrapper .stats-left {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.employment-status .stats-wrapper .stats-legend-color {
  height: 5px;
  width: 9px;
  content: "";
  margin-right: 3px;
}

.employment-status .stats-wrapper .stats-title {
  font-size: 0.7rem;
  font-weight: 600;
}

.employment-status .stats-wrapper .stats-percent {
  font-size: 0.7rem;
  font-weight: 600;
  color: var(--black-83);
}
.employment-status .stats-wrapper .stats-value {
  font-size: 0.7rem;
  font-weight: 600;
  color: var(--black-83);
}

.recharts-tooltip-wrapper {
  z-index: 111 !important;
}

.events {
  grid-column: 7 / 9;
  grid-row: 6 / 26;
  display: flex;
  flex-direction: column;
  background-color: var(--pure-white);
}

.events .header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.events .header .see-all-cta {
  font-size: 0.6rem;
  color: var(--black-83);
  font-weight: 600;
}

.events-contents-wrapper {
  flex: 1;

  display: flex;
  flex-direction: column;
}
.datepicker-wrapper {
  height: 260px;
}

.events-list-wrapper {
  display: flex;
  flex-direction: column;
  flex: 1;
}
.events-list-wrapper .event-selected-date {
  text-align: left;
  font-size: 0.8rem;
  margin: 10px 0;
}

.events-list-wrapper .events-list {
  overflow-y: auto;
}

.events-list .event-time {
  color: var(--black-83);
  font-size: 0.5rem;
  width: 55px;
  min-width: 55px;
  text-align: left;
}

.events-list .event {
  display: flex;
  flex-direction: column;
  border-left: 1px solid var(--white-e9);
  padding-left: 12px;
}

.events-list .event-name {
  font-size: 0.7rem;
  text-align: left;
  font-weight: 600;
}

.events-list .event-dept {
  margin-top: 3px;
  font-size: 0.6rem;
  text-align: left;
  text-transform: capitalize;
  border-radius: 4px;
  text-align: left;
  width: fit-content;
  padding: 2px 5px;
}

@media screen and (max-width: 1055px) {
  .greeting-card .left .greet {
    font-size: 1.2rem;
  }

  .premium-card .bg-image {
    width: 50px;
  }

  .premium-card .content .content-title {
    font-size: 0.9rem;
  }
}

@media screen and (max-width: 980px) {
  .greeting-card .right .card {
    width: 150px;
  }
  .premium-card .bg-image {
    width: 40px;
  }
}

@media screen and (max-width: 938px) {
  .greeting-card .left .date {
    font-size: 0.6rem;
  }
  .greeting-card .left .greet {
    font-size: 1rem;
  }

  .greeting-card .right .card {
    width: 120px;
  }
}

@media screen and (max-width: 920px) {
  .dashboard {
    width: 100%;
    overflow-y: auto;
    display: grid;
    grid-template-rows: repeat(35, 1fr);
    grid-template-columns: repeat(8, 1fr);
    gap: 12px;
  }

  .premium-card .content .content-title {
    font-size: 0.85rem;
    line-height: 18px;
  }

  .premium-card .subscription-btn {
    margin-top: 6px;
  }

  .KPI {
    grid-column: 1/5;
  }
  .attendance {
    grid-column: 5/9;
  }

  .task-card {
    grid-column: 1/5;
    grid-row: 15/25;
  }
  .employment-status {
    grid-column: 5/9;
    grid-row: 15/25;
  }

  .employee-satisfaction {
    grid-column: 1/4;
    grid-row: 25/36;
  }

  .stats-wrapper .range-graph input[type="range"] {
    width: 70%;
  }

  .events {
    grid-column: 4/9;
    grid-row: 25/36;
    min-height: 100%;
  }

  .events .events-contents-wrapper {
    flex: 1;
    flex-direction: row;
  }

  .datepicker-wrapper {
    height: 285px;
    margin-right: 10px;
  }

  .events-list-wrapper .event-selected-date {
    margin: 0 0 10px 0;
  }
}

@media screen and (max-width: 900px) {
  .premium-card .bg-image {
    width: 30px;
  }
  .premium-card .subscription-btn {
    padding: 8px;
    font-size: 0.6rem;
    font-weight: 600;

    margin-top: 10px;
  }
}

@media screen and (max-width: 820px) {
  .greeting-card .left .date {
    font-size: 0.55rem;
  }

  .greeting-card .left .greet {
    font-size: 0.8rem;
  }

  .greeting-card .left .greet-desc {
    font-size: 0.55rem;
  }

  .greeting-card .right .card {
    height: 80%;
    width: 110px;
    background-size: 20%;
    font-size: 0.7rem;
    display: none;
  }

  .greeting-card .right .card + .card {
    margin-left: 10px;
  }

  .greeting-card .right .card .icon {
    border-radius: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 0.7rem;
    padding: 0.4rem;
  }

  .greeting-card .right .card .count {
    font-size: 1.1rem;
  }

  .greeting-card .right .card .desc {
    font-size: 0.5rem;
  }

  .greeting-card .right .mini-card {
    display: flex;
    flex-direction: column;
    width: 100%;
    background-color: var(--base-white);
    margin-left: 20px;
    padding: 10px;
    justify-content: space-around;
    border-radius: 10px;
    height: 80%;
  }

  .greeting-card .right .mini-card .icon {
    font-size: 0.7rem;
    border-radius: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 20px;
    width: 20px;
    margin-right: 10px;
  }

  .greeting-card .right .mini-card .count {
    font-size: 1rem;
    font-weight: 600;
  }

  .greeting-card .right .mini-card .desc {
    font-size: 0.7rem;
  }

  .premium-card .content .content-title {
    font-size: 0.7rem;
    line-height: 18px;
  }
}

@media screen and (max-width: 800px) {
  .datepicker-wrapper {
    width: 45%;
  }
}

@media screen and (max-width: 747px) {
  .premium-card .content .content-title {
    font-size: 0.9rem;
  }
  .premium-card .content-desc {
    display: none;
  }
  .premium-card .subscription-btn {
    margin-top: 10px;
  }
}

@media screen and (max-width: 680px) {
  .x-axis-tick {
    font-size: 7px;
  }

  .y-axis-tick {
    font-size: 7px;
  }
}

@media screen and (max-width: 620px) {
  .greeting-card .right .mini-card {
    overflow-y: auto;
    height: 90%;
  }
  .greeting-card .right .mini-card > div + div {
    margin-top: 10px;
  }

  .greeting-card .right .mini-card .desc {
    text-align: left;
    font-size: 0.7rem;
  }
  .greeting-card .right .mini-card .count {
    font-size: 1rem;
    font-weight: 600;
  }

  .dashboard {
    grid-template-rows: repeat(65, 1fr);
    grid-template-columns: repeat(8, 1fr);
  }
  .premium-card {
    grid-column: 1/9;
    grid-row: 1/5;
  }
  .premium-card .bg-image {
    width: 40px;
  }
  .premium-card .content .content-title {
    font-size: 0.9rem;
  }
  .premium-card .content .content-desc {
    font-size: 0.6rem;
    max-width: 250px;
    display: block;
  }
  .greeting-card {
    grid-column: 1/9;
    grid-row: 5/10;
  }
  .greeting-card .left .greet {
    font-size: 1.2rem;
  }

  .KPI {
    grid-column: 1/9;
    grid-row: 10/20;
  }
  .attendance {
    grid-column: 1/9;
    grid-row: 20/30;
  }
  .task-card {
    grid-column: 1/9;
    grid-row: 30/39;
  }

  .employee-satisfaction {
    grid-column: 1/9;
    grid-row: 39/48;
  }

  .employment-status {
    grid-column: 1/9;
    grid-row: 48/57;
  }

  .employee-satisfaction .stats-wrapper,
  .employment-status .stats-wrapper {
    padding: 0 5%;
  }

  .events {
    grid-column: 1/9;
    grid-row: 57/66;
  }
}

@media screen and (max-width: 580px) {
  .greeting-card .right {
    padding: 10px 10px 10px 0px;
  }
  .greeting-card .right .mini-card .count {
    font-size: 0.9rem;
    font-weight: 600;
  }
}

@media screen and (max-width: 480px) {
  .dashboard {
    height: 380vh;
    grid-template-rows: repeat(65, 1fr);
    grid-template-columns: repeat(8, 1fr);
  }

  .KPI {
    grid-row: 10/18;
  }
  .attendance {
    grid-row: 18/26;
  }
  .task-card {
    grid-row: 26/34;
  }

  .employee-satisfaction {
    grid-row: 34/43;
  }

  .employment-status {
    grid-row: 43/52;
  }

  .events {
    display: flex;
    flex-direction: column;
    grid-row: 52/66;
  }
  .events .header {
    margin-bottom: 5px;
  }
  .events .events-contents-wrapper {
    flex-direction: column;
  }
  .datepicker-wrapper {
    width: 100%;
    padding: 0 10%;
  }

  .event-selected-date {
    margin-top: 8px;
  }
}

@media screen and (max-width: 450px) {
  .search {
    position: relative;
    background-color: var(--pure-white);
    border-radius: 20px;
    display: none;
  }
  .greeting-card .right .mini-card {
    min-width: 155px;
  }
  .greeting-card .right .mini-card .icon {
    display: none;
  }
}

@media screen and (max-width: 420px) {
  .greeting-card .left .greet {
    font-size: 0.85rem;
  }
}

@media screen and (min-height: 1000px) {
  .dashboard {
    max-height: 110vh;
  }
}
@media screen and (max-height: 1000px) {
  .dashboard {
    min-height: 700px;
    max-height: 800px;
  }
}

@media screen and (max-width: 920px) and (max-height: 1000px) {
  .dashboard {
    min-height: 700px;
    max-height: 1130px;
  }
}
@media screen and (max-width: 620px) {
  .dashboard {
    max-height: 2000px;
  }
  .datepicker-wrapper {
    height: 213px;
  }
}
