.datepicker {
  background-color: var(--pure-white);
  border-bottom: 1px solid var(--white-dd);
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 5px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  width: 100%;
}

.nav-button {
  padding: 4px;
  margin-right: 8px;
  background-color: var(--white-f0);
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.nav-button:hover {
  background-color: var(--white-e0);
}

.current-month {
  font-size: 14px;
  text-align: center;
}

.days-row {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  width: 100%;
}

.day-name {
  text-align: center;
  font-size: 12px;
  color: var(--black-81);
  font-weight: 600;
}

.cells {
  display: flex;
  flex-direction: column;
  flex: 1;
  justify-content: space-between;
  width: 100%;
}

.row {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  margin: 1.5px 0px;
}

.datepicker-cell {
  text-align: center;
  font-size: 12px;
  font-weight: 600;
  cursor: pointer;
  padding: 0.5rem 0;
  margin: 0 2px;
  border-radius: 4px;
}

.datepicker-cell.disabled {
  color: var(--white-e0);
}

.datepicker-cell.disabled:hover {
  background-color: var(--pure-white);
}

.datepicker-cell.today {
  color: var(--theme-color);
  border: 1px solid var(--theme-color);
}

.datepicker-cell.today.selected {
  color: var(--pure-white);
  background-color: var(--theme-color);
}

.datepicker-cell.selected {
  color: var(--pure-white);
  background-color: var(--theme-color);
}

.datepicker-cell:hover {
  color: var(--pure-white);
  background-color: var(--theme-color);
}

@media screen and (max-width: 1050px) {
  .datepicker-cell {
    padding: 0.2rem;
  }
}

@media screen and (max-width: 920px) {
  .datepicker-cell {
    padding: 0.5rem;
  }
}

@media screen and (max-width: 800px) {
  .datepicker-cell {
    padding: 0.2rem;
  }
}
